.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 150;
}
.header_bg{
    background-color: rgba(0, 0, 0, 0.95);
    transition: 0.5s;
}

.nav_menu{
    display: flex;

}
.logo{
    cursor: pointer;
}
.logo:hover{
    color: dodgerblue;
    transition: 0.5s;
}
.nav_menu li{
    padding: 0 1rem;
}
.nav_menu li a{
    font-size: 1.2rem;
    font-weight: 500;
    cursor: pointer;
    
}
.nav_menu li a:hover{
    color: dodgerblue;
    transition: .5s;
    
}


.hamburger{
    display: none;
}
@media screen and (max-width:640px) {
.nav_menu{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: .25s;
    
}
.nav_menu.active{
    left: 0;
}
.nav_menu li{
    padding: 1rem 0;
}
.nav_menu li a{
    font-size: 2rem;
}
.hamburger{
    display: initial;
}
}